/**
 * A data sharing policy defines how a data collection is shared between sites in the same Wix account.
 *
 * The policy specifies which collection to share, optional filtering rules to control which items are accessible, and serves as the access credential for other sites to connect to the shared collection. Multiple policies can be created for the same collection with different filtering rules or access patterns.
 */
export interface DataSharingPolicy {
  /**
   * Data sharing policy ID.
   * @format GUID
   * @readonly
   */
  id?: string;
  /**
   * ID of the data collection to share.
   * @immutable
   * @maxLength 255
   */
  dataCollectionId?: string;
  /**
   * Filter to restrict which items from the collection are shared.
   *
   * Use [API Query Language syntax](https://dev.wix.com/api/rest/getting-started/api-query-language) to define filtering rules. If not specified,
   * all items in the collection are shared.
   */
  dataItemsFilter?: Record<string, any> | null;
  /**
   * Date and time the data sharing policy was created.
   * @readonly
   */
  createdDate?: Date | null;
  /**
   * Date and time the data sharing policy was last updated.
   * @readonly
   */
  updatedDate?: Date | null;
}

export interface SharedCollectionConnectedEvent {
  /** Shared connection that was created. */
  connection?: SharingConnection;
}

/**
 * A sharing connection represents the connection between 2 sites via a shared data collection.
 *
 * The connection is created when a site connects to a shared collection using a data sharing policy. It contains information about both the source site that owns the collection and created the policy, and the target site that the policy is shared with and now has access to the collection. It also includes the namespace used to access the shared data on the target site.
 */
export interface SharingConnection {
  /**
   * ID of the data collection owned and shared by the source site.
   * @maxLength 255
   */
  sourceDataCollectionId?: string;
  /**
   * ID of the source site that owns the collection and created the policy.
   * @format GUID
   */
  sourceSiteId?: string;
  /**
   * ID of the shared collection on the target site.
   * The ID includes the namespace. For example, `"namespace/collectionName"`.
   * @maxLength 255
   */
  targetDataCollectionId?: string;
  /**
   * ID of the target site that the collection is shared with.
   * @format GUID
   */
  targetSiteId?: string;
  /**
   * Optional display name for the namespace that appears on the target site.
   * @maxLength 100
   */
  targetDisplayNamespace?: string | null;
  /**
   * ID of the data sharing policy that controls this connection.
   * @format GUID
   */
  dataSharingPolicyId?: string;
  /**
   * Date and time the connection was created.
   * @readonly
   */
  createdDate?: Date | null;
  /**
   * Date and time the connection was last updated.
   * @readonly
   */
  updatedDate?: Date | null;
}

export interface SharedCollectionDisconnectedEvent {
  /** Shared connection that was removed. */
  connection?: SharingConnection;
}

export interface CreateDataSharingPolicyRequest {
  /** Data sharing policy to create. */
  dataSharingPolicy?: DataSharingPolicy;
}

export interface CreateDataSharingPolicyResponse {
  /** Created data sharing policy. */
  dataSharingPolicy?: DataSharingPolicy;
}

export interface UpdateDataSharingPolicyRequest {
  /** Data sharing policy to update. */
  dataSharingPolicy: DataSharingPolicy;
}

export interface UpdateDataSharingPolicyResponse {
  /** Updated data sharing policy. */
  dataSharingPolicy?: DataSharingPolicy;
}

export interface DeleteDataSharingPolicyRequest {
  /**
   * Data sharing policy ID.
   * @format GUID
   */
  dataSharingPolicyId: string;
}

export interface DeleteDataSharingPolicyResponse {}

export interface GetDataSharingPolicyRequest {
  /**
   * Data sharing policy ID.
   * @format GUID
   */
  dataSharingPolicyId: string;
}

export interface GetDataSharingPolicyResponse {
  /** Retrieved data sharing policy. */
  dataSharingPolicy?: DataSharingPolicy;
}

export interface ListDataSharingPoliciesRequest {
  /**
   * List of collection IDs to filter policies by.
   *
   * When provided, only returns policies for the specified collections.
   * @maxSize 100
   * @maxLength 255
   */
  dataCollectionIds?: string[];
  /**
   * Paging information.
   * By default, policies are sorted by `id` in ascending order.
   */
  paging?: CursorPaging;
}

export interface CursorPaging {
  /**
   * Number of items to load.
   * @max 100
   */
  limit?: number | null;
  /**
   * Pointer to the next or previous page in the list of results.
   *
   * You can get the relevant cursor token
   * from the `pagingMetadata` object in the previous call's response.
   * Not relevant for the first request.
   * @maxLength 16000
   */
  cursor?: string | null;
}

export interface ListDataSharingPoliciesResponse {
  /** List of data sharing policies. */
  dataSharingPolicies?: DataSharingPolicy[];
  /** Paging metadata. */
  pagingMetadata?: CursorPagingMetadata;
}

export interface CursorPagingMetadata {
  /** Number of items returned in the response. */
  count?: number | null;
  /** Offset that was requested. */
  cursors?: Cursors;
  /**
   * Indicates if there are more results after the current page.
   * If `true`, another page of results can be retrieved.
   * If `false`, this is the last page.
   */
  hasNext?: boolean | null;
}

export interface Cursors {
  /**
   * Cursor pointing to next page in the list of results.
   * @maxLength 16000
   */
  next?: string | null;
  /**
   * Cursor pointing to previous page in the list of results.
   * @maxLength 16000
   */
  prev?: string | null;
}

export interface ListSharedDataCollectionsRequest {
  /**
   * List of collection IDs to filter by.
   *
   * When provided, only returns shared connections for the specified collections.
   * @maxSize 100
   * @maxLength 255
   */
  dataCollectionIds?: string[];
  /**
   * Whether to list collections shared with the current site or shared from the current site.
   * @deprecated
   * @replacedBy shared_with_current_site
   * @targetRemovalDate 2026-12-31
   */
  sharedWithCurrent?: boolean;
  /**
   * Paging information.
   * By default, shared collections are sorted by `id` in ascending order.
   */
  paging?: CursorPaging;
  /**
   * Whether to list collections shared with the current site or shared from the current site.
   * When `true`, lists collections shared with the current site. When `false`, lists collections shared from the current site's policies.
   *
   * Default: `false`
   */
  sharedWithCurrentSite?: boolean;
}

export interface ListSharedDataCollectionsResponse {
  /** List of shared collections. */
  connections?: SharingConnection[];
  /** Paging metadata. */
  pagingMetadata?: CursorPagingMetadata;
}

export interface ConnectToSharedCollectionRequest {
  /**
   * Namespace to connect collection with on the target site.
   * Must be unique in the site.
   * @minLength 1
   * @maxLength 100
   */
  namespace: string;
  /**
   * Optional display name for the namespace that appears on the target site.
   * @maxLength 100
   */
  displayNamespace?: string | null;
  /**
   * Data sharing policy ID to connect with.
   * @format GUID
   */
  dataSharingPolicyId?: string;
}

export interface ConnectToSharedCollectionResponse {
  /** Created connection to shared data collection. */
  connection?: SharingConnection;
}

export interface DisconnectFromSharedCollectionRequest {
  /**
   * Shared data collection ID.
   * @maxLength 255
   * @deprecated
   * @replacedBy target_data_collection_id
   * @targetRemovalDate 2026-12-31
   */
  dataCollectionId?: string;
  /**
   * ID of the shared collection on the target site.
   * @maxLength 255
   */
  targetDataCollectionId?: string;
}

export interface DisconnectFromSharedCollectionResponse {}

export interface DomainEvent extends DomainEventBodyOneOf {
  createdEvent?: EntityCreatedEvent;
  updatedEvent?: EntityUpdatedEvent;
  deletedEvent?: EntityDeletedEvent;
  actionEvent?: ActionEvent;
  /** Event ID. With this ID you can easily spot duplicated events and ignore them. */
  id?: string;
  /**
   * Fully Qualified Domain Name of an entity. This is a unique identifier assigned to the API main business entities.
   * For example, `wix.stores.catalog.product`, `wix.bookings.session`, `wix.payments.transaction`.
   */
  entityFqdn?: string;
  /**
   * Event action name, placed at the top level to make it easier for users to dispatch messages.
   * For example: `created`/`updated`/`deleted`/`started`/`completed`/`email_opened`.
   */
  slug?: string;
  /** ID of the entity associated with the event. */
  entityId?: string;
  /** Event timestamp in [ISO-8601](https://en.wikipedia.org/wiki/ISO_8601) format and UTC time. For example, `2020-04-26T13:57:50.699Z`. */
  eventTime?: Date | null;
  /**
   * Whether the event was triggered as a result of a privacy regulation application
   * (for example, GDPR).
   */
  triggeredByAnonymizeRequest?: boolean | null;
  /** If present, indicates the action that triggered the event. */
  originatedFrom?: string | null;
  /**
   * A sequence number that indicates the order of updates to an entity. For example, if an entity was updated at 16:00 and then again at 16:01, the second update will always have a higher sequence number.
   * You can use this number to make sure you're handling updates in the right order. Just save the latest sequence number on your end and compare it to the one in each new message. If the new message has an older (lower) number, you can safely ignore it.
   */
  entityEventSequence?: string | null;
}

/** @oneof */
export interface DomainEventBodyOneOf {
  createdEvent?: EntityCreatedEvent;
  updatedEvent?: EntityUpdatedEvent;
  deletedEvent?: EntityDeletedEvent;
  actionEvent?: ActionEvent;
}

export interface EntityCreatedEvent {
  entityAsJson?: string;
  /** Indicates the event was triggered by a restore-from-trashbin operation for a previously deleted entity */
  restoreInfo?: RestoreInfo;
}

export interface RestoreInfo {
  deletedDate?: Date | null;
}

export interface EntityUpdatedEvent {
  /**
   * Since platformized APIs only expose PATCH and not PUT we can't assume that the fields sent from the client are the actual diff.
   * This means that to generate a list of changed fields (as opposed to sent fields) one needs to traverse both objects.
   * We don't want to impose this on all developers and so we leave this traversal to the notification recipients which need it.
   */
  currentEntityAsJson?: string;
}

export interface EntityDeletedEvent {
  /** Entity that was deleted. */
  deletedEntityAsJson?: string | null;
}

export interface ActionEvent {
  bodyAsJson?: string;
}

export interface Empty {}

export interface MessageEnvelope {
  /**
   * App instance ID.
   * @format GUID
   */
  instanceId?: string | null;
  /**
   * Event type.
   * @maxLength 150
   */
  eventType?: string;
  /** The identification type and identity data. */
  identity?: IdentificationData;
  /** Stringify payload. */
  data?: string;
}

export interface IdentificationData extends IdentificationDataIdOneOf {
  /**
   * ID of a site visitor that has not logged in to the site.
   * @format GUID
   */
  anonymousVisitorId?: string;
  /**
   * ID of a site visitor that has logged in to the site.
   * @format GUID
   */
  memberId?: string;
  /**
   * ID of a Wix user (site owner, contributor, etc.).
   * @format GUID
   */
  wixUserId?: string;
  /**
   * ID of an app.
   * @format GUID
   */
  appId?: string;
  /** @readonly */
  identityType?: WebhookIdentityTypeWithLiterals;
}

/** @oneof */
export interface IdentificationDataIdOneOf {
  /**
   * ID of a site visitor that has not logged in to the site.
   * @format GUID
   */
  anonymousVisitorId?: string;
  /**
   * ID of a site visitor that has logged in to the site.
   * @format GUID
   */
  memberId?: string;
  /**
   * ID of a Wix user (site owner, contributor, etc.).
   * @format GUID
   */
  wixUserId?: string;
  /**
   * ID of an app.
   * @format GUID
   */
  appId?: string;
}

export enum WebhookIdentityType {
  UNKNOWN = 'UNKNOWN',
  ANONYMOUS_VISITOR = 'ANONYMOUS_VISITOR',
  MEMBER = 'MEMBER',
  WIX_USER = 'WIX_USER',
  APP = 'APP',
}

/** @enumType */
export type WebhookIdentityTypeWithLiterals =
  | WebhookIdentityType
  | 'UNKNOWN'
  | 'ANONYMOUS_VISITOR'
  | 'MEMBER'
  | 'WIX_USER'
  | 'APP';
